<template>
  <div id="app">
    <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
      <a class="navbar-brand" href="#">MemberInfo</a>
      <button
        class="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>

      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav mr-auto">
         

        </ul>
        <div class="form-inline mr-3">
          <div>Filter:</div>
          <select name="Filter" id="" class="form-control" @change="changeStatus" v-model="status">
            <option value="top10">top10</option>
            <option value="popular20">popular20</option>
          </select>
        </div>
      </div>
    </nav>
    <router-view :status="myStatus"></router-view>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      status: "",
      myStatus: ""
    };
  },
  methods: {
    changeStatus() {
      this.myStatus = this.status;
    }
  }
};
</script>



<style lang="scss">
@import "./assets/all";
</style>



