<template>
  <div class="hello">
   
    <table class="table table-striped table-dark container-fluid">
	  <thead >FILTER: {{status}}</thead>
      <thead>
        <tr>
          <th scope="col">No.</th>
          <th scope="col">Photo</th>
          <th scope="col">Name</th>
          <th scope="col">Email</th>
          <th scope="col">Age</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item , index) in listData10" v-if="status==='top10'">
          <th scope="row">{{index + 1}}</th>
          <td>
            <img :src="item.picture.thumbnail" :alt="item.name.first+item.name.last" />
          </td>
          <td>{{item.name.title}} {{item.name.first}} {{item.name.last}}</td>
          <td>{{item.email}}</td>
          <td>{{item.dob.age}}</td>
        </tr>
		<tr v-for="(item , index) in listData20" v-if="status==='popular20'">
          <th scope="row">{{index + 1}}</th>
          <td>
            <img :src="item.picture.thumbnail" :alt="item.name.first+item.name.last" />
          </td>
          <td>{{item.name.title}} {{item.name.first}} {{item.name.last}}</td>
          <td>{{item.email}}</td>
          <td>{{item.dob.age}}</td>
        </tr>
		<tr v-for="(item , index) in listData" v-if="status===''">
          <th scope="row">{{index + 1}}</th>
          <td>
            <img :src="item.picture.thumbnail" :alt="item.name.first+item.name.last" />
          </td>
          <td>{{item.name.title}} {{item.name.first}} {{item.name.last}}</td>
          <td>{{item.email}}</td>
          <td>{{item.dob.age}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  name: "name",
  props: ["status"],
  data() {
    return {
	  listData: [],
	  listData10: [],
	  listData20: [],
    };
  },
  mounted() {
	let vm = this;
    $.ajax({
        url: "https://randomuser.me/api/?results=50",
        dataType: "json",
        success: function(data) {
          console.log(data.results);
          vm.listData = data.results;
        }
	});
	$.ajax({
        url: "https://randomuser.me/api/?results=10",
        dataType: "json",
        success: function(data) {
          console.log(data.results);
          vm.listData10 = data.results;
        }
	});
	$.ajax({
        url: "https://randomuser.me/api/?results=20",
        dataType: "json",
        success: function(data) {
          console.log(data.results);
          vm.listData20 = data.results;
        }
    });
  }
};
</script>

<style>
</style>


