import Vue from 'vue'
import Router from 'vue-router'
import List from '@/components/list'
import Home from '@/components/Home'


Vue.use(Router)

export default new Router({
  routes: [
	{
		path: '/',
		component:  List,
	},
	{
		path: '/list',
		component: List,
	},
  ]
})



